<template>
  <div class="login">
    <div class="back" @click="back">
      <img class="icon-back" src="./img/toBack.png" alt="返回">
    </div>
    <div class="login-wrapper" v-show="!userid">
      <h2 class="title">login</h2>
      <div class="user-input inputs">
        <img class="togo" src="./img/username-icon.png" alt="搜索" title="搜索">
        <input ref="query" v-model="username" class="input-box" :placeholder="placeholderuser">
        <img
          v-show="username"
          class="toclear"
          src="./img/toclear.png"
          alt="清除"
          title="清空"
          @click="clearuser"
        >
      </div>
      <div class="pass-input inputs">
        <img class="togo" src="./img/password-icon.png" alt="搜索" title="搜索">
        <input
          ref="query"
          type="password"
          v-model="password"
          class="input-box"
          :placeholder="placeholderpass"
        >
        <img
          v-show="password"
          class="toclear"
          src="./img/toclear.png"
          alt="清除"
          title="清空"
          @click="clearpass"
        >
      </div>
      <div class="submit" @click="submitinfo">登 录</div>
    </div>
    <div class="userinfo" v-show="userid">
      <div class="app">
        <div class="bg-box" :style="bgStyle">
          <div class="header" :style="agStyle">
            <div class="header-filter">
              <!-- <img src="./img/toBack.png" alt="返回" title="返回" @click="toBack"> -->
              <h1>{{userinfo.nickname}}</h1>
            </div>
          </div>
          <div class="filter" ref="filter">
            <!-- <span @click="punch" class="punch">{{punchtext}}</span> -->
            <div class="content">
              <h2 class="saying">世界这么大，遇见你真好~</h2>
              <span class="logout" @click="logout">退出登录</span>
            </div>
            <!-- <h1 >{{userinfo.nickname}}</h1> -->
          </div>
        </div>
        <div class="userplayer">
          <span>正在完善，敬请期待</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { login, logout, userInfo } from "api/user";
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      userinfo: "",
      username: "tianjianen2015@163.com",
      password: "m123,kidfkk3j99654hufdsdgccentmtian1ejiansmenlpaf",
      // encodepassword:'',
      placeholderuser: "请输入邮箱或手机号",
      placeholderpass: "请输入密码"
      // 签到延迟实现
      // punchstatus: '',
      // punchtext: '签到'
    };
  },
  computed: {
    ...mapGetters(["userid"]),
    bgStyle() {
      return `background-image:url(${this.userinfo.backgroundUrl})`;
    },
    agStyle() {
      return `background-image:url(${this.userinfo.avatarUrl})`;
    }
  },
  watch: {
    userid(newVal, oldVal) {
      if (newVal) {
        userInfo(newVal).then(res => {
          this.userinfo = res.profile;
        });
      }
    }
  },
  methods: {
    // punch() {
    //   Punch().then(res => {
    //     console.log(res);
    //     this.punchstatus = res.code;
    //     if (this.punchstatus !== 3) {
    //       this.punchtext = '已签到';
    //     }
    //   })
    // },
    back() {
      this.$router.back();
    },
    clearuser() {
      this.username = "";
    },
    clearpass() {
      this.password = "";
    },
    submitinfo() {
      let userStatus = false;
      console.log(123);
      console.log(this.username);
      console.log(this.password);
      console.log(this.encodepassword);
      userStatus = this.checkusername();
      if (userStatus) {
        login(this.username, this.password)
          .then(res => {
            console.log(res);
            this.saveUser(res.account.id);
            // this.punch();
          })
          .then(res => {
            this.$router.push("/recommend");
          });
      }
    },
    logout() {
      this.clearUser();
      this.$router.push("/recommend");
      logout().then(res => {
        console.log('log out');
      });
    },
    checkusername() {
      let emailFlag = false;
      let phoneFlag = false;
      let phoneReg = /(^1[3|4|5|7|8]\d{9}$)|(^09\d{8}$)/;
      let emailReg = /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,4})$/;
      if (this.username.search(emailReg) !== -1) {
        emailFlag = true;
        return true;
      }
      if (phoneReg.test(this.username)) {
        phoneFlag = true;
        return true;
      }
      if (emailFlag && phoneFlag) {
        return true;
      }
      if (!emailFlag) {
        console.log("邮箱输入错误");
        return false;
      }
      if (!phoneFlag) {
        console.log("手机号输入错误");
        return false;
      }
      return true;
    },
    ...mapActions(["saveUser", "clearUser"])
  },
  mounted() {
    if (this.userid) {
      console.log("123111");
    }
  }
};
</script>

<style scoped lang="scss">
@import "assets/css/mixin.scss";

.login {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 100;
  width: 100%;
  background: #222;
}
.back {
  position: absolute;
  top: 0;
  left: rem(6);
  z-index: 500;
}
.icon-back {
  // display: block;
  // padding: rem(10);
  // font-size: rem(22);
  // color: #ffcd32;
  width: rem(30);
  height: rem(30);
  position: absolute;
  top: rem(15);
  left: rem(15);
}
.title {
  margin-top: rem(80);
  text-align: center;
  color: #ffcd32;
  font-size: rem(30);
}
.inputs {
  // margin-top: rem(20);
  // margin-left: rem(20);
  // margin-right: rem(20);
  margin: rem(20);
}
.submit {
  align-items: center;
  box-sizing: border-box;
  border-radius: rem(6);
  color: #ffcd32;
  // display: flex;
  text-align: center;
  // width: 100%;
  padding: rem(6);
  height: rem(40);
  background: #333;
  border-radius: rem(6);
  margin: rem(20);
  font-size: rem(26);
  line-height: rem(26);
}
.user-input {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  // width: 100%;
  padding: 0 rem(6);
  height: rem(40);
  background: #333;
  border-radius: rem(6);
}
.user-input img {
  width: rem(18);
  height: rem(18);
}
.icon-search {
  font-size: rem(24);
  color: #222;
}

.pass-input {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  // width: 100%;
  padding: 0 rem(6);
  height: rem(40);
  background: #333;
  border-radius: rem(6);
}
.pass-input img {
  width: rem(18);
  height: rem(18);
}
.input-box {
  flex: 1;
  margin: 0 rem(5);
  line-height: rem(18);
  background: #333;
  color: #fff;
  font-size: rem(14);
  outline: none;
  border: none;
  text-indent: rem(5);
}

.box::placeholder {
  color: #fff;
}
.icon-dismiss {
  font-size: rem(16);
  color: #222;
}

.app {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgb(34, 34, 34);
  z-index: 301;
  color: #fff;
}
.header {
  width: 30%;
  background-size: 100% auto;
  position: relative;
  z-index: 300;
  height: 40%;
  border-radius: 100%;
  margin: auto;
  top: rem(40);
}
.header img {
  width: rem(30);
  height: rem(30);
  position: absolute;
  top: rem(10);
  left: rem(15);
}
.header h1 {
  width: 100%;
  height: rem(45);
  text-align: center;
  padding-left: rem(45);
  line-height: rem(45);
  font-size: rem(18);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  position: fixed;
  top: 20%;
  left: -11%;
}
.bg-box {
  width: 100%;
  height: 39vh;
  background-size: 100% auto;
  position: relative;
}
.filter {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(7, 17, 27, 0.4);
}
.content {
  position: relative;
  bottom: 0px;
  font-size: rem(18);
  text-align: center;
  margin: auto;
  top: 70%;
  color: #ffcd32;
}
.logout {
  display: inline-block;
  margin-top: rem(20);
  color: #fff;
  padding: rem(10);
  border: rem(2) solid #222;
  border-radius: rem(5);
  background: #333;
}
.scroll {
  width: 100%;
  height: 61vh;
}
.header-filter {
  width: 100%;
  height: 100%;
  background: rgba(7, 17, 27, 0.4);
  // border-bottom:1px solid rgba(7, 17, 27, 0.4);
}
.userplayer {
  text-align: center;
  font-size: rem(18);
  margin: rem(100);
  opacity: 0.6;
}
.punch{
  display: inline-block;
  font-size: rem(16);
  color: #ffcd32;
  border: rem(1) solid #222;
  padding: rem(10);
  border-radius: rem(10);
  background: #333;
  position: absolute;
  right: rem(10);
  top: rem(10);
}
</style>



