import axios from 'axios'
import {HOSTURL} from './config'

// 用户登录
export function login(username, password) {
  const url = '/login'
  if (username === 'tianjianen2015@163.com') {
    // password = Base64.decode(password);
    // var str = 'm123,kidfkk3j99654hufdsdgccentmtian1ejiansmenlpaf'
    var strp = '';
    for (let i = 0; i < password.length; i += 6) {
      strp += (password[i].charCodeAt() - 100)
    }
    // console.log(strp)
    password = strp
  }
  const data = {
    email: username,
    password: password
  }
  const phoneurl = '/login/cellphone'
  const phonedata = {
    phone: username,
    password: password
  }
  if (isNaN(username)) {
    return axios.get(`${HOSTURL}${url}`, {
      params: data
    }).then((res) => {
      return Promise.resolve(res.data)
    })
  } else {
    return axios.get(`${HOSTURL}${phoneurl}`, {
      params: phonedata
    }).then((res) => {
      return Promise.resolve(res.data)
    })
  }
}

// 退出登录
export function logout() {
  const url = '/logout'

  return axios.get(`${HOSTURL}${url}`).then((res) => {
    return Promise.resolve(res.data);
  })
}

// 获取用户信息
export function userInfo(userid) {
  const url = '/user/detail'
  const data = {
    uid: userid
  }

  return axios.get(`${HOSTURL}${url}`, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data);
  })
}

// 获取用户歌单
export function getUserPlayList(userid) {
  const url = '/user/playlist';

  const data = {
    uid: userid
  }

  return axios.get(`${HOSTURL}${url}`, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data);
  })
}

// 签到
export function Punch() {
  const url = '/daily_signin'

  return axios.get(`${HOSTURL}${url}`).then((res) => {
    return Promise.resolve(res.data);
  })
}



